(function() {
  window.exampleApp = angular.module("exampleApp", []);

}).call(this);

(function() {
  var ctrl;

  ctrl = function($scope) {
    return $scope.products = [
      {
        name: "Apples",
        category: "Fruit",
        price: 1.20,
        expiry: 10
      }, {
        name: "Bananas",
        category: "Fruit",
        price: 2.42,
        expiry: 7
      }, {
        name: "Pears",
        category: "Fruit",
        price: 2.02,
        expiry: 6
      }
    ];
  };

  angular.module("exampleApp").controller("FirstCtrl", ["$scope", ctrl]);

}).call(this);

(function() {
  angular.module("exampleApp").directive("unorderedList", function() {
    return function(scope, element, attrs) {
      var data, i, item, len, listElem, results;
      data = scope[attrs["unorderedList"]];
      if (angular.isArray(data)) {
        listElem = angular.element("<ul>");
        element.append(listElem);
        results = [];
        for (i = 0, len = data.length; i < len; i++) {
          item = data[i];
          results.push(listElem.append(angular.element('<li>').text(item.name)));
        }
        return results;
      }
    };
  });

}).call(this);
